import React, { PureComponent } from 'react'
import { BrowserRouter as Router, Switch, Route, NavLink, BrowserRouter } from 'react-router-dom';
import SubmitButton from '../SubmitButton';
import UserStore    from '../stores/UserStore';
import { Link } from 'react-router-dom';
import PersediaanScreen from './PersediaanScreen';
import PemesananScreen from './PemesananScreen';
import SupplierScreen from './SupplierScreen';
import ResepScreen from './ResepScreen';
import StokCokelatScreen from './StokCokelatScreen';
import KeuanganScreen from './KeuanganScreen';

class DashboardScreen extends React.Component{
    async componentDidMount(){
        try{
          let res = await fetch('/isLoggedIn', {
            method: 'post',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }
          });
    
          let result = await res.json();
    
          if (result && result.success){
            UserStore.loading = false;
            UserStore.isLoggedIn = true;
            UserStore.username = result.username;
          }
          else{
            UserStore.loading = false;
            UserStore.isLoggedIn = false;
          }
        }
        catch(e){
          UserStore.loading = false;
          UserStore.isLoggedIn = false;
        }
      }
    
      async doLogout(){
        try{
          let res = await fetch('/logout', {
            method: 'post',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }
          });
    
          let result = await res.json();
    
          if (result && result.success){
            UserStore.isLoggedIn = false;
            UserStore.username = '';
          }
        }
        catch(e){
          console.log(e);
        }
      }
    render(){
        return (<div>
            <BrowserRouter>
            <div className="container">
                <Route path="/pemesanan" component={PemesananScreen} />
                <Route path="/persediaan" component={PersediaanScreen} />
                <Route path="/supplier" component={SupplierScreen} />
                <Route path="/resep" component={ResepScreen} />
                <Route path="/stokcokelat" component={StokCokelatScreen} />
                <Route path="/keuangan" component={KeuanganScreen} />
                Welcome, {UserStore.username}.
                <div className='tombol'>
                    <Link to={'/pemesanan'}>
                    <button className='btn'>Pemesanan Cokelat</button>
                    </Link>
                </div>
    
                <div className='tombol'>
                    <Link to={'/persediaan'}>
                    <button className='btn'>Persediaan Bahan</button>
                    </Link>
                </div>
    
                <div className='tombol'>
                    <Link to={'/supplier'}>
                    <button className='btn'>Halaman Supplier</button>
                    </Link>
                </div>
                
                <div className='tombol'>
                    <Link to={'/resep'}>
                    <button className='btn'>Resep</button>
                    </Link>
                </div>
    
                <div className='tombol'>
                    <Link to={'/stokcokelat'}>
                    <button className='btn'>Stok Cokelat</button>
                    </Link>
                </div>
    
                <div className='tombol'>
                    <Link to={'/keuangan'}>
                    <button className='btn'>Catatan Keuangan</button>
                    </Link>
                </div>
    
                <SubmitButton 
                text={'Log Out'}
                disabled = {false}
                onClick = {() => this.doLogout() }
                />
                </div>
            </BrowserRouter>
        </div>);
    }
}

export default DashboardScreen;